import React from 'react';
import './App.css';
import * as utils from '../utils'

// App
import { Navbar, Card, Button, OverlayTrigger, Tooltip, Modal, Form, Row, Col, InputGroup, Table, FormControl } from 'react-bootstrap';
// import uuid4 from 'uuid/v4'
// import uuidToHex from 'uuid-to-hex'
import Cookies from 'universal-cookie';
// import { GoogleLogin } from 'react-google-login';
// import { GoogleLogin, GoogleLogout } from 'react-google-login';
import FontAwesome from 'react-fontawesome';
import 'font-awesome/css/font-awesome.min.css';

class App extends React.Component {
  constructor(props) {
    super(props);

    // get data from local storage
    let server = window.localStorage.getItem("server");
    if (server == null) {
      server = "http://localhost:5000/";
      window.localStorage.setItem("server", server);
    }
    let endpoint = window.localStorage.getItem("endpoint");
    if (endpoint == null) {
      endpoint = "http://sitaware.isi.edu:8080/bigdata/namespace/wdq/sparql";
      window.localStorage.setItem("endpoint", endpoint);
    }

    // init app
    this.state = {
      showSettings: false,
      showNewProject: false,

      // temp in form
      tempNewProject: "",
      tempSearch: "",
      tempServer: server,
      tempEndpoint: endpoint,

      // projects
      // projectData: [],
      projectData: [
        { pid: "1111", ptitle: "Project 1", cdate: "2019-08-01" },
        { pid: "2222", ptitle: "Project 2", cdate: "2019-08-02" },
        { pid: "3333", ptitle: "Project 3", cdate: "2019-08-03" },
      ],
    };
    window.server = server;
    window.endpoint = endpoint;
  }

  render() {
    return (
      <div>
        {this.renderNewProject()}
        {this.renderSettings()}

        {/* navbar */}
        <div>
          <Navbar className="shadow" bg="dark" variant="dark" sticky="top" style={{ height: "50px" }}>

            {/* logo */}
            {/* <Navbar.Brand className="pl-2 font-weight-bold" style={{ cursor: "help" }} title="Table to Wikidata Mapping Language">
              T2WML&nbsp;GUI
            </Navbar.Brand> */}
            <OverlayTrigger placement="bottom" trigger="hover"
              // defaultShow="true"
              overlay={
                <Tooltip style={{ width: "fit-content" }}>
                  <div className="text-left">
                    Table to Wikidata<br />
                    Mapping Language
                  </div>
                </Tooltip>
              }
            >
              <Navbar.Brand className="pl-2 font-weight-bold" style={{ cursor: "help" }}>
                T2WML&nbsp;GUI
              </Navbar.Brand>
            </OverlayTrigger>
            <Navbar.Text className="pr-2" style={{ cursor: "default" }}>
              v1.4&nbsp;(beta)
            </Navbar.Text>

            {/* settings */}
            <Navbar.Text className="pr-2 ml-auto" style={{ verticalAlign: "center" }}>
              <Button
                className="d-inline-block float-right"
                variant="outline-light"
                size="sm"
                style={{ padding: "0rem 0.5rem" }}
                onClick={this.handleShowSettings.bind(this)}
              >Settings</Button>
            </Navbar.Text>

          </Navbar>
        </div>

        {/* content */}
        <div style={{ height: "calc(100vh - 50px)", background: "#f8f9fa", paddingTop: "20px" }}>
          <Card className="shadow-sm" style={{ width: "90%", maxWidth: "800px", height: "calc(100vh - 90px)", margin: "0 auto" }}>
            <Card.Header style={{ height: "40px", padding: "0.5rem 1rem", background: "#343a40" }}>
              <div
                className="text-white font-weight-bold d-inline-block text-truncate"
                style={{ width: "100%", cursor: "default" }}
              >Your Projects</div>
            </Card.Header>
            <Card.Body style={{ padding: "5% 5%", overflowY: "auto" }}>

              <div style={{ marginBottom: "5%" }}>
                <div style={{ display: "inline-block", width: "40%" }}>
                  <Button
                    variant="primary"
                    style={{ fontWeight: "bold" }}
                    onClick={this.handleShowNewProject.bind(this)}
                  >New Project</Button>
                </div>
                <div style={{ display: "inline-block", width: "60%" }}>
                  <InputGroup>
                    <InputGroup.Prepend>
                      <InputGroup.Text style={{ background: "whitesmoke" }}>
                        <FontAwesome name="search" />
                      </InputGroup.Text>
                    </InputGroup.Prepend>
                    <FormControl placeholder="Search projects..." onChange={(event) => this.setState({ tempSearch: event.target.value })} />
                  </InputGroup>
                </div>
              </div>
              {this.renderProjects()}
            </Card.Body>
          </Card>
        </div>
      </div>
    );
  }

  renderNewProject() {
    return (
      <Modal show={this.state.showNewProject} onHide={() => { /* do nothing */ }}>

        {/* header */}
        <Modal.Header style={{ background: "whitesmoke" }}>
          <Modal.Title>New Project</Modal.Title>
        </Modal.Header>

        {/* body */}
        <Modal.Body>
          <Form className="container">

            {/* project title */}
            <Form.Group as={Row} style={{ marginTop: "1rem" }} onChange={(event) => this.setState({ tempNewProject: event.target.value })}>
              <Form.Label column sm="12" md="2" className="text-right">
                Title
              </Form.Label>
              <Col sm="12" md="10">
                <Form.Control type="text" defaultValue="" />
              </Col>
            </Form.Group>

          </Form>
        </Modal.Body>

        {/* footer */}
        <Modal.Footer style={{ background: "whitesmoke" }}>
          <Button variant="outline-dark" onClick={this.handleHideNewProject.bind(this)}>
            Cancel
          </Button>
          <Button variant="dark" onClick={this.handleDoNewProject.bind(this)} disabled={(this.state.tempNewProject === "" ) ? true : false}>
            Create
          </Button>
        </Modal.Footer>

      </Modal>
    );
  }

  renderProjects() {
    const { projectData, tempSearch } = this.state;
    const keywords = tempSearch.toLowerCase().split(/ +/);

    let projectListDiv = [];
    for (let i = 0, len = projectData.length; i < len; i++) {
      const { pid, ptitle, cdate } = projectData[i];
      if (utils.searchProject(ptitle, keywords)) {
        projectListDiv.push(
          <tr key={i}>
            <td>
              <span>
                <a
                  href={"http://localhost:3000/project/" + pid}
                  target="_blank"
                  rel="noopener noreferrer"
                  style={{ "color": "hsl(200, 100%, 30%)" }}
                >{ptitle}</a>
              </span>
            </td>
            <td>
              {cdate}
            </td>
          </tr>
        );
      }
    }
    if (projectListDiv.length === 0) {
      projectListDiv.push(
        <tr key={-1}>
          <td colSpan="2" style={{ textAlign: "center" }}>No porjects</td>
        </tr>
      );
    }
    return (
      <Table bordered hover responsive size="sm" style={{ fontSize: "14px" }}>
        <thead style={{ background: "whitesmoke" }}>
          <tr>
            <th style={{ width: "60%" }}>Title</th>
            <th style={{ width: "40%" }}>Date Created</th>
          </tr>
        </thead>
        <tbody>
          {projectListDiv}
        </tbody>
      </Table>
    );
  }

  renderSettings() {
    return (
      <Modal show={this.state.showSettings} size="lg" onHide={() => { /* do nothing */ }}>

        {/* header */}
        <Modal.Header style={{ background: "whitesmoke" }}>
          <Modal.Title>Settings</Modal.Title>
        </Modal.Header>

        {/* body */}
        <Modal.Body>
          <Form className="container">

            {/* project id */}
            <Form.Group as={Row} style={{ marginTop: "1rem" }}>
              <Form.Label column sm="12" md="3" className="text-right">
                Project&nbsp;ID
              </Form.Label>
              {/* <Col sm="12" md="9">
                <Form.Control type="text" defaultValue={window.pid} readOnly />
              </Col> */}
              <Col sm="12" md="9">
                <InputGroup>
                  <Form.Control type="text" defaultValue={window.pid} readOnly />
                  <InputGroup.Append>
                    <Button variant="outline-danger" onClick={() => {
                      this.handleClear();
                      window.location.reload();
                    }}>â†»</Button>
                  </InputGroup.Append>
                </InputGroup>
              </Col>
            </Form.Group>

            {/* server */}
            <Form.Group as={Row} onChange={(event) => this.setState({ tempServer: event.target.value })}>
              <Form.Label column sm="12" md="3" className="text-right">
                Server
              </Form.Label>
              <Col sm="12" md="9">
                <Form.Control type="url" defaultValue={window.server} />
              </Col>
            </Form.Group>

            {/* query server */}
            <Form.Group as={Row}>
              <Form.Label column sm="12" md="3" className="text-right">
                SPARQL&nbsp;endpoint
              </Form.Label>
              <Col sm="12" md="9">
                <Form.Control as="select"
                  onChange={(event) => this.setState({ tempEndpoint: event.target.value })}
                  defaultValue={window.endpoint}
                >
                  <option value="http://sitaware.isi.edu:8080/bigdata/namespace/wdq/sparql">http://sitaware.isi.edu:8080/bigdata/namespace/wdq/sparql</option>
                  <option value="http://kg2018a.isi.edu:8888/bigdata/namespace/wdq/sparql">http://kg2018a.isi.edu:8888/bigdata/namespace/wdq/sparql</option>
                  <option value="https://query.wikidata.org/sparql">https://query.wikidata.org/sparql</option>
                </Form.Control>
              </Col>
            </Form.Group>

            {/* cache (for clear qnode cache feature) */}
            {/* <Form.Group as={Row}>
              <Form.Label column sm="12" md="3" className="text-right">
                Cache
              </Form.Label>
              <Col sm="12" md="9">
                <Button variant="secondary" onClick={() => {
                  window.Wikifier.setState({ cacheQnode: {} });
                  alert("Emptied qnode cache.");
                  window.Wikifier.updateCacheQnode();
                }}>
                  Empty qnode cache
                </Button>
              </Col>
            </Form.Group> */}

          </Form>
        </Modal.Body>

        {/* footer */}
        <Modal.Footer style={{ background: "whitesmoke" }}>
          <Button variant="outline-dark" onClick={this.handleHideSettings.bind(this)}>
            Cancel
          </Button>
          <Button variant="dark" onClick={this.handleDoSettings.bind(this)}>
            Save
          </Button>
        </Modal.Footer>

      </Modal>
    );
  }

  handleClear() {
    // notify backend
    let formData = new FormData();
    formData.append("id", window.pid);
    const api = window.server + "delete_user";
    fetch(api, {
      body: formData,
      credentials: "same-origin", // "same-origin", "include"
      method: "POST"
    });

    // remove cookie
    const cookies = new Cookies();
    cookies.remove("pid");
  }

  handleDoNewProject(event) {
    console.log("<App> created new project: %c" + this.state.tempNewProject, "background: yellow; color: black");
    this.setState({ showNewProject: false });

    // TODO: create new project
  }

  handleDoSettings(event) {
    console.log("<App> updated settings");
    window.server = this.state.tempServer;
    window.localStorage.setItem("server", window.server);
    window.endpoint = this.state.tempEndpoint;
    window.localStorage.setItem("endpoint", window.endpoint);
    this.setState({ showSettings: false });

    // notify backend
    let formData = new FormData();
    formData.append("id", window.pid);
    formData.append("endpoint", window.endpoint);
    const api = window.server + "update_setting";
    fetch(api, {
      body: formData,
      credentials: "same-origin", // "same-origin", "include"
      method: "POST"
    });
  }

  handleHideNewProject(event) {
    this.setState({ showNewProject: false });
  }

  handleHideSettings(event) {
    this.setState({ showSettings: false });
  }

  handleShowNewProject(event) {
    this.setState({ showNewProject: true });
  }

  handleShowSettings(event) {
    this.setState({ showSettings: true });
  }
}

export default App;
